.facet-wrapper {
  display: block;
  background: #fff;
  border-radius: 5px;
  margin: 10px 10px 0 60px;
}


.facet {

  &__header {
    position: relative;
    padding: 10px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    &:hover {
      background-color: #f7f7f7;
    }

    &:active {
      background-color: #f1f1f1;
    }

    //&:after {
    //  position: absolute;
    //  top: 50%;
    //  left: 10px;
    //  width: 10px;
    //  height: 10px;
    //  margin-top: -5px;
    //  content: "";
    //  text-align: center;
    //  background-image: url('../images/selectdown.svg');
    //  background-repeat: no-repeat;
    //  background-position: 50% 50%;
    //}

    //&_closed {
    //
    //  &:after {
    //    transform: rotate(180deg);
    //  }
    //
    //}
  }

  &__title {
    position: relative;
    padding: 0;
    margin: 0;
    font: 500 20px Roboto,sans-serif;
    color: #000;
  }

  &__body {
    padding: 10px;

    &_closed {
      display: none;
    }

  }

  &__link {
    display: block;
    text-decoration: none;
    margin-bottom: 10px;
    font: 400 14px Roboto,sans-serif;
    color: #2196F3;

    &:hover {
      text-decoration: underline;
    }
  }

  &__checkbox {
    position: relative;
    display: block;
    color: #000;
    font: 400 14px Roboto,sans-serif;
    margin-bottom: 10px;

    label {
      display: block;
      max-width: 100%;
      cursor: pointer;
      padding-left: 20px;
    }

    input[type=checkbox] {
      position: absolute;
      top: 3px;
      margin-left: -20px;
    }
  }

  &__radio {
    position: relative;
    display: block;
    font: 400 14px Roboto,sans-serif;
    color: #000;
    margin-bottom: 10px;

    label {
      display: block;
      margin-top: -5px;
      max-width: 100%;
      cursor: pointer;
      padding-left: 20px;
    }

    input[type=radio] {
      position: absolute;
      top: 3px;
      margin-left: -20px;
    }
  }

  &__input {
    position: relative;
    display: block;
    font: 400 14px Roboto,sans-serif;
    color: #000;

    label {
      display: block;
      max-width: 100%;
      cursor: pointer;
    }

    &_line {

      input {
        display: inline-block;
        position: relative;
        width: 100%;
        background-color: #fff;
        border: 1px #d7d7d7 solid;
        padding: 5px;
        border-radius: 3px;
        outline: none;
        margin-bottom: 10px;

        &:focus {
          border-color: #2196F3;
        }
      }

    }

    &_group {

      input {
        display: inline-block;
        position: relative;
        width: 49%;
        background-color: #fff;
        border: 1px #d7d7d7 solid;
        padding: 5px;
        border-radius: 3px;
        outline: none;
        margin-bottom: 10px;

        &:focus {
          border-color: #2196F3;
        }

        &:last-child {
          float: right;
        }

      }

    }
  }

  &__btn {
    position: relative;
    text-align: center;
    text-decoration: none;
    display: block;
    width: 100%;
    font-size: 0.875em;
    font-weight: 300;
    background-color: #fff;
    color: #444;
    border: 1px #b1b1b1 solid;
    border-radius: 3px;
    padding: 5px 10px;
    outline: none;

    &:hover {
      background-color: #f7f7f7;
    }

    &:active {
      background-color: #f1f1f1;
    }
  }

  &__slider {
    width: 100%;
    height: 20px;
    margin-bottom: 5px;
    z-index: 0;

    & .handle {
      font-size: 0.9em;
      text-align: center;
      background-color: #fff;
      color: white;
      cursor: pointer;
      border: 1px solid #909eab;
      border-radius: 50%;
      z-index: 0;
    }

    .handle.active {
      background-color: #f1f1f1;
      border-color: #2196F3;
    }

    .bar {
      position: relative;
      background: #ddd;
      cursor: pointer;
    }

    .bar.bar-1 {
      background: #b1b1b1;
    }

    .bar.bar-2 {
      background: #ddd;
    }

    & .bar {
      top: 8px;
      height: 1px;
    }

    & .handle {
      width: 18px;
      height: 18px;
    }

  }
}
